    @input=\"videoPlayerStore.toggleMute\"\n               class=\"sr-only\"/>\n\n        <!-- volume on icon -->\n        <svg class=\"swap-off fill-current icon\"\n             xmlns=\"http://www.w3.org/2000/svg\"\n             width=\"48\"\n             height=\"48\"\n             viewBox=\"0 0 24 24\">\n          <path\n              d=\"M14,3.23V5.29C16.89,6.15 19,8.83 19,12C19,15.17 16.89,17.84 14,18.7V20.77C18,19.86 21,16.28 21,12C21,7.72 18,4.14 14,3.23M16.5,12C16.5,10.23 15.5,8.71 14,7.97V16C15.5,15.29 16.5,13.76 16.5,12M3,9V15H7L12,20V4L7,9H3Z\"\n              stroke=\"#000\"\n              stroke-width=\"0.5\"/>\n        </svg>\n\n        <!-- volume off icon -->\n        <svg class=\"swap-on fill-current icon\"\n             xmlns=\"http://www.w3.org/2000/svg\"\n             width=\"48\"\n             height=\"48\"\n             viewBox=\"0 0 24 24\">\n          <path\n              d=\"M3,9H7L12,4V20L7,15H3V9M16.59,12L14,9.41L15.41,8L18,10.59L20.59,8L22,9.41L19.41,12L22,14.59L20.59,16L18,13.41L15.41,16L14,14.59L16.59,12Z\"\n              stroke=\"#000\"\n              stroke-width=\"0.5\"\n          />\n        </svg>\n\n      </label>\n    </div>\n\n    <div class=\"icon-container\">\n      <button\n          class=\"cursor-help\"\n          @click=\"prev\"\n      >\n        <svg\n            width=\"48\"\n            height=\"48\"\n            viewBox=\"0 0 24 24\"\n            class=\"fill-curren icon\"\n            xmlns=\"http://www.w3.org/2000/svg\"\n        >\n          <path d=\"M18 17L10 12L18 7V17Z\"\n                stroke=\"#000\"\n                stroke-width=\"0.5\"/>\n          <path d=\"M6 7H9V17H6V7Z\"\n                stroke=\"#000\"\n                stroke-width=\"0.5\"/>\n        </svg>\n      </button>\n    </div>\n\n    <div class=\"icon-container\">\n      <label class=\"swap\">\n\n        <!-- this hidden checkbox controls the state -->\n        <input type=\"checkbox\"\n               v-model=\"videoPlayerStore.paused\"\n               @input=\"videoPlayerStore.togglePlay\"\n               class=\"sr-only\"/>\n\n        <!-- play icon -->\n        <svg class=\"swap-on fill-current icon\"\n             xmlns=\"http://www.w3.org/2000/svg\"\n             width=\"48\"\n             height=\"48\"\n             viewBox=\"0 0 24 24\">\n          <path d=\"M8,5.14V19.14L19,12.14L8,5.14Z\"\n                stroke=\"#000\"\n                stroke-width=\"0.5\"/>\n        </svg>\n\n        <!-- pause icon -->\n        <svg class=\"swap-off fill-current icon\"\n             xmlns=\"http://www.w3.org/2000/svg\"\n             width=\"48\"\n             height=\"48\"\n             viewBox=\"0 0 24 24\">\n          <path d=\"M6,19H10V5H6V19M12,5V19H16V5H12Z\"\n                stroke=\"#000\"\n                stroke-width=\"0.5\"/>\n        </svg>\n\n      </label>\n    </div>\n\n    <div class=\"icon-container\">\n      <button\n          class=\"cursor-help\"\n          @click=\"next\"\n      >\n        <svg\n            width=\"48\"\n            height=\"48\"\n            viewBox=\"0 0 24 24\"\n            class=\"fill-current icon\"\n            xmlns=\"http://www.w3.org/2000/svg\"\n        >\n          <path d=\"M6 17L14 12L6 7V17Z\"\n                stroke=\"#000\"\n                stroke-width=\"0.5\"/>\n          <path d=\"M18 7H15V12V17H18V7Z\"\n                stroke=\"#000\"\n                stroke-width=\"0.5\"/>\n        </svg>\n      </button>\n    </div>\n\n    <!-- Temporary Info Modal -->\n    <dialog id=\"nextPrevButtonsInfoModal\" class=\"modal\">\n      <div class=\"modal-box text-center my-auto\" data-theme=\"dark\">\n        <h3 class=\"font-bold text-3xl\">Hello!</h3>\n        <p class=\"py-4 text-xl\">Playlists and channel navigation are coming soon.</p>\n        <div class=\"modal-action justify-center w-full\">\n          <form method=\"dialog\">\n            <!-- if there is a button in form, it will close the modal -->\n            <button class=\"btn\">Okay</button>\n          </form>\n        </div>\n      </div>\n    </dialog>\n\n  </div>\n\n</template>\n\n<script setup>\nimport { Inertia } from \"@inertiajs/inertia\";\nimport { useVideoPlayerStore } from \"@/Stores/VideoPlayerStore\"\nimport { useAppSettingStore } from \"@/Stores/AppSettingStore\"\nconst appSettingStore = useAppSettingStore()\n\nconst videoPlayerStore = useVideoPlayerStore()\n\nfunction next() {\n  document.getElementById('nextPrevButtonsInfoModal').showModal()\n  // videoPlayerStore.next()\n}\n\nfunction prev() {\n  document.getElementById('nextPrevButtonsInfoModal').showModal()\n  // videoPlayerStore.back()\n}\n\nfunction fullscreen() {\n  videoPlayerStore.fullscreen()\n}\n\n</script>\n\n<style scoped>\n\n.sr-only {\n  position: absolute;\n  overflow: hidden;\n  clip: rect(0, 0, 0, 0);\n  width: 1px;\n  height: 1px;\n  margin: -1px;\n  padding: 0;\n  border: 0;\n}\n\n/* Normal state styles for the icon */\n.icon-container {\n  display: inline-block;\n  color: #f59e0b;\n  @apply hover:bg-yellow-400\n}\n\n.icon {\n  fill: currentColor; /* Use the current text color as the icon color */\n  transition: fill 0.3s ease; /* Smooth color transition */\n}\n\n/* Hover state styles for the icon */\n.icon-container:hover .icon {\n  fill: #d08509; /* Change to text-yellow-500 color on hover */\n}\n\n\n</style>\n"],"sourceRoot":""}]);
